export default {
	data() {
		return {
			outCode: '',
			chlidCodeArr: []
		}
	},
	created() {
		
	},
	methods: {
		createdClick(){
			uni.scanCode({
				success: (res) => {
					this.$common.tip("扫码成功", "success")
					this.outCode = res.result
				}
			});
		},
		confirm() {
			if (!this.outCode) {
				this.$common.tip("外码不能为空", "none")
				return;
			}
			if (this.chlidCodeArr.length === 0) {
				this.$common.tip("子码不能为空", "none")
				return;
			}

			let param = {
				outCode: this.outCode,
				subCodeList: this.chlidCodeArr
			}
			this.$common.post("/trace-api/trace/relationOutCode", param).then((res) => {
				console.log("relationOutCode", res)
				if (Number(res.data.code) === 200) {

					uni.showToast({
						title: res.data.data,
						duration: 2000
					});
					setTimeout(()=>{
						uni.navigateBack({
							delta: 1
						});
					},1500)
				} else {

					uni.showToast({
						title: res.data.data,
						duration: 2000,
						icon: "none"
					});
				}
			})
		},
		deleteCode(index) {
			uni.showModal({
				title: '提示',
				content: '此操作将删除此子码编号',
				success: (res) => {
					if (res.confirm) {
						this.chlidCodeArr.splice(index, 1);
						this.$common.tip("删除成功", "success")
					}
				}
			});

		},
		relation() {
			uni.scanCode({
				success: (res) => {
					this.$common.tip("扫码成功", "success")
					this.chlidCodeArr.push(res.result)
				}
			});
		}
	}

}
